
<template>
    <div id="app">
        <ul class="menu">
            <!-- menuItems => 뷰 컴포넌트내에 정의된 데이터 배열
             menuItems 배열의 각 요소가 아이템 변수로 전달되어 사용됨 -->
            <li v-for="(item, index) in menuItems" :key="index">
                <a href="#" class="menu_item">
                    <span class="menu_icon">
                        <!-- :clas - 뷰의 바인딩 구문 (클래스 동적 적용)
                         item 객체 안에 있는 icon 속성 변경
                         아이콘 값에 따라 해당 아이콘이 렌더링됨 -->
                        <i :class="item.icon"></i>
                    </span>
                    <span class="menu_text">
                        {{item.text}}
                    </span>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                menuItems: [
                { icon: 'xi-refresh', text: '되돌리기' },
                { icon: 'xi-arrow-down', text: '다운받기' },
                { icon: 'xi-lock', text: '잠금' },
                { icon: 'xi-tags', text: '태그' },
                { icon: 'xi-mail', text: '메일' }
                ]
            };
        }
    };
</script>

<style>
    :root {
        --btn-bg: #ddd;
        /* 앞 색상은 왼쪽, 뒤 색상은 오른쪽 */
        --btn-hover-bg: linear-gradient(92.23deg, #cc25e8 16.87%, #0af0ff 95.69%);
        --btn-color: #666;
        --btn-hover-color: #fff;
        --btn-icon-bg: #fff;
        --btn-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    }

    /* :root - CSS에서 최상위 요소
        * SASS 문법과 같은 전역변수처럼 사용됨
        * 전체 페이지에서 일관된 스타일 관리, 재사용성 향상, 유지보수 용이, 스타일 통일감 */

    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    ul {
        list-style: none;
    }

    a {
        text-decoration: none;
    }

    body {
        /* font-family: Arial, Helvetica, sans-serif; */
        /* font-family: 'Nanum Gothic' !important; 최우선순위로 적용 */
        /* CSS우선순위: !important > id > class > tag */
        font-family: 'Nanum Gothic';
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
        background-color: #fff;
    }

    ul.menu {
        display: flex;
        flex-direction: column;
        gap: 15px;
    }

    ul.menu li a.menu_item {
        display: flex;
        align-items: center;
        width: 160px;
        padding: 5px 6px;
        background-color: var(--btn-bg);
        color: var(--btn-color);
        border-radius: 30px;
        transition: 0.3s;
    }

    ul.menu li a.menu_item:hover,
    ul.menu li a.menu_item:focus {
        background: var(--btn-hover-bg);
        color: var(--btn-hover-color);
        box-shadow: var(--btn-shadow);
    }

    ul.menu li a.menu_item span.menu_icon {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 10px;
        transition: 0.3s;
        background-color: var(--btn-icon-bg);
    }

    ul.menu li a.menu_item:hover span.menu_icon {
        color: #ababab;
    }

    ul.menu li a.menu_item:hover span.menu_icon {
        color: #000;
    }

    ul.menu li a.menu_item span.menu_text {
        font-size: 14px;
    }
</style>